import {Col, Row, Spacer} from "@nextui-org/react";
import ArrowDown from "../../images/ArrowDown.svg"
import {useRef, useState} from "react";

export default function TCollapse({title, content}) {
    const [expanded, setExpanded] = useState(false);

    const ref = useRef();

    return (
        <Row>
            <Col>
                <Row align={"center"} onClick={() => setExpanded(!expanded)} css={{cursor: "pointer"}}>
                    {title}
                    <Spacer x={0.5}/>
                    <img src={ArrowDown} style={{
                        height: "21px",
                        transition: "300ms",
                        transform: `rotate(${expanded ? 180 : 0}deg)`
                    }}/></Row>
                    <Row css={{
                        pl: "20px",
                        transition: "300ms",
                        overflowY: "hidden",
                        height: expanded ? ref.current.offsetHeight + 5 + "px" : "0"}}>
                        <div ref={ref}>
                            <Col>
                                {content}
                            </Col>
                        </div>

                    </Row>
            </Col>
        </Row>
    );
}