import {Col, Container, Navbar, Row, Spacer, styled, Text} from "@nextui-org/react";
import logo from '../../images/SimpleLogoWithoutBackground.svg'
import TDropdown from "../Common/TDropdown";
import TText from "../Common/TText";
import TCollapse from "../Common/TCollapse";
import PushkinButton from "./PushkinButton";
import EyeButton from "./EyeButton";
import useWindowSize from "../../hooks/useWindowSize";
import TContainer from "../Common/TContainer/TContainer";

export default function Header({heightClb}) {
    const windowSize = useWindowSize();

    if (!windowSize.height) {
        return <div style={{height: "220px", width: "100%"}}></div>
    }

    if (windowSize.width < 960) {
        heightClb(76)
        return <MobileHeader/>
    } else {
        heightClb(220)
        return <DesktopHeader windowSize={windowSize}/>
    }
}

function MobileHeader() {
    const titleFontCss = {
        fontSize: "27px",
        weight: 500,
        cursor: "pointer"
    };

    const clickable = {
        fontSize: "25px",
        cursor: "pointer",
        padding: "5px 10px",
        "&:hover": {
            border: "1px solid",
        }
    }
    const collapseItems = [
        <TText light css={titleFontCss}>Афиша</TText>,
        <TCollapse
            title={<TText light css={titleFontCss}>Репертуар</TText>}
            content={
            <ul style={{margin: "0"}}>
                <li><TText light css={clickable}>Текущий</TText></li>
                <li><TText light css={clickable}>По Пушкинской карте</TText></li>
                <li><TText light css={clickable}>Архив</TText></li>
            </ul>}
        />,
        <TCollapse
            title={<TText light css={titleFontCss}>О театре</TText>}
            content={
            <ul style={{margin: "0"}}>
                <li><TText light css={clickable}>Коллектив</TText></li>
                <li><TText light css={clickable}>История</TText></li>
            </ul>}
        />,
        <TText light css={titleFontCss}>Визит в театр</TText>,
        <TText light css={titleFontCss}>Новости</TText>,
    ];

    const logoFontCSS = {fontFamily: "DIN Condensed", fontSize: "25px", lineHeight: "90%"}

    return (
        <Navbar disableShadow>
            <Navbar.Brand>
                <img src={logo} width={"30px"}/>
            </Navbar.Brand>
            <Navbar.Content>
                <Col>
                <Row>
                    <Text css={logoFontCSS}>АРМАВИРСКИЙ ТЕАТР</Text>
                </Row>
                <Row>
                    <Text css={logoFontCSS}>ДРАМЫ И КОМЕДИИ</Text>
                </Row>
                </Col>
            </Navbar.Content>
            <Navbar.Toggle showIn={"lg"}/>
            <Navbar.Collapse >
                {collapseItems.map((item, index) => (
                    <Navbar.CollapseItem
                        key={item}
                    >
                        {item}
                    </Navbar.CollapseItem>
                ))}
            </Navbar.Collapse>
        </Navbar>
    )
}

function DesktopHeader({windowSize}) {
   const logoFontCSS = {fontFamily: "DIN Condensed", fontSize: "45px", lineHeight: "0.9em"}
    const overallHeight = 220;
   const logoRowHeight = 0.7 * overallHeight;
   const buttonsRowHeight = overallHeight - logoRowHeight;
   const TTextHeader = styled(Text, {
       fontSize: "32px",
       fontFamily: "DIN Condensed",
       fontWeight: 300,
       cursor: "pointer"
   });


   return (
       <Row css={{h: `${overallHeight}px`}}>
           <TContainer>
              <Row justify={"center"} css={{h: `${logoRowHeight}px`}} align={"center"}>
                  <img src={logo} style={{width: "55px", marginRight: "10px"}}/>
                  <Col css={{w:"max-content"}}>
                      <TText size={45}>АРМАВИРСКИЙ ТЕАТР</TText>
                      <TText size={45} style={{letterSpacing: "-1px"}}>ДРАМЫ И КОМЕДИИ</TText>
                  </Col>
              </Row>
               <Row justify={"space-between"} css={{h: ""}} align={"center"}>
                   <Col>
                       <Row justify={"flex-start"} align={"center"} css={{h: `${buttonsRowHeight}px`, w: "max-content"}}>
                           <TTextHeader>Афиша</TTextHeader>
                           <Spacer x={2}/>
                           <TDropdown
                               title={<TTextHeader>Репертуар</TTextHeader>}
                               items={[
                                   {key: "current", title: "Текущее"},
                                   {key: "pushkin", title: "По Пушкинской карте"},
                                   {key: "archive", title: "Архив"},
                               ]}
                               onAction={key => {console.log(key)}}
                           />
                           <Spacer x={2}/>
                           <TDropdown
                               title={<TTextHeader>О театре</TTextHeader>}
                               items={[
                                   {key: "employees", title: "Коллектив"},
                                   {key: "history", title: "История"},
                               ]}
                               onAction={key => {console.log(key)}}
                           />
                           <Spacer x={2}/>
                           <TTextHeader>Визит в театр</TTextHeader>
                           <Spacer x={2}/>
                           <TTextHeader>Новости</TTextHeader>
                       </Row>
                   </Col>
                   <Col css={{w: "min-content"}}>
                       <Row justify={"flex-end"}>
                           <PushkinButton simple={windowSize.width < 1280}/>
                           {windowSize.width >= 1280 && <Spacer x={1}/>}
                           <EyeButton simple={windowSize.width < 1280}/>
                       </Row>
                   </Col>
               </Row>
           </TContainer>
       </Row>
   )
}