import {Card, Row} from "@nextui-org/react";
import TText from "./TText";

function TCard({img, title, action, theme}) {
    return (
        <Card variant={"flat"} isPressable css={styles.card} onClick={action}>
            <Card.Body css={{p: 0}}>
                <Card.Image
                    src={img}
                    width="100%"
                    height="100%"
                    objectFit="cover"
                    alt="Card example background"
                />
            </Card.Body>
            <Card.Footer css={{...styles.cardFooter, background: theme === "dark" ? "$darkBackground" : "white"}}>
                <Row justify={"flex-start"}>
                    <TText css={{...styles.title, color: theme === "dark" ? "white" : "black"}}>{title}</TText>
                </Row>
            </Card.Footer>
        </Card>
    )
}

export default TCard;

const styles = {
    card: {
        h: '400px',
        w: '225px',
        borderRadius: 0,
        '@xs': {
            h: '500px',
            w: '280px'
        },
        '@lg': {
            h: '600px',
            w: '340px'
        }
    },
    cardFooter: {
        background: "$darkBackground",
        borderRadius: 0,
        pt: '$lg',
        pl: 0,
        pr: 0,
        pb: 0,
        '@sm': {
            pb: "$lg"
        }
    },
    title: {
        fontSize: '$3xl',
        '@sm': {
            fontSize: '$3xl'
        },
        '@md': {
            fontSize: '$4xl'
        },
        '@lg': {
            fontSize: '$5xl'
        }
    }
}