import BlockTitle from "../BlockTitle";
import HorizontalSlider from "../../Common/Slider/HorizontalSlider";
import FullContainer from "../../Common/FullContainer";
import {useEffect, useMemo} from "react";
import TContainer from "../../Common/TContainer/TContainer";
import useValue from "../../../hooks/useValue";
import axios from "../../../lib/axios/axios";
import {Col} from "@nextui-org/react";
import {API} from "../../../api/api";
import useElementSize from "../../../hooks/useElementSize";
import useDeviceSize from "../../../hooks/useDeviceSize";
import useWindowSize from "../../../hooks/useWindowSize";
import TCard from "../../Common/TCard";

function News({}) {
    const deviceSize = useDeviceSize();
    const items = useValue([]);

    useEffect(() => {
        axios.get(API.newsSlider)
            .then(resp => {
                items.set(resp.data);
            })
            .catch(() => {

            })
    }, [])

    const cards = useMemo(() => {
        return items.value.map(item => (<TCard title={item.title} img={item.img} action={() => {}}/>));
    }, [items])

    const windowSize = useWindowSize();
    const [titleRef, titleSize] = useElementSize();

    return (
        <FullContainer css={{background: 'white'}}>
            <TContainer>
                <BlockTitle color={"tblack"} ref={titleRef} css={{mt: "50px"}}>Новости</BlockTitle>
                <Col css={{
                    h: (windowSize.height - (titleSize.top + titleSize.height)) + "px",
                    w: "100%",
                    display: "flex", flexFlow: "column", justifyContent: "center"}}>
                    <HorizontalSlider
                        items={cards}
                        severalComponentOnSlide={deviceSize !== "xxs"}
                        numberOfComponentOnSlide={deviceSize === "xs" ? 2 : 3}
                    />
                </Col>
            </TContainer>
        </FullContainer>
    );
}

export default News;