import Header from "../Header/Header";
import {Row} from "@nextui-org/react";
import Slider from "./Slider/Slider";
import FullPageScroller from "../FullPageScroller/FullPageScroller";
import FullPageContainer from "../FullPageScroller/FullPageContainer";
import useWindowSize from "../../hooks/useWindowSize";
import {useState} from "react";
import Poster from "./Poster/Poster";
import Shows from "./Shows/Shows";
import News from "./News/News";
import Footer from "./Footer/Footer";

export default function MainPage() {
    const windowSize = useWindowSize();
    const [headerHeight, setHeaderHeight] = useState(76);
    return (
        <FullPageScroller>
            <FullPageContainer>
                <Header heightClb={setHeaderHeight}/>
                <Row css={{h: `${windowSize.height - headerHeight}px`}}>
                    <Slider/>
                </Row>
            </FullPageContainer>
            <FullPageContainer>
                <Poster/>
            </FullPageContainer>
            <FullPageContainer>
                <Shows/>
            </FullPageContainer>
            <FullPageContainer>
                <News/>
            </FullPageContainer>
            <FullPageContainer>
                <Footer/>
            </FullPageContainer>
        </FullPageScroller>
    )
}