import useElementSize from "../../../hooks/useElementSize";
import LongArrow from "../../../images/LongArrow.svg"
import RightArrow from "../../../images/RightArrow.svg"
import SmallArrowDown from "../../../images/ArrowDown.svg"
import {SliderElement} from "./SliderElement";
import useValue from "../../../hooks/useValue";
import {forwardRef, useCallback, useEffect, useMemo} from "react";
import {Col, Row} from "@nextui-org/react";
import SlideContainer from "./SlideContainer";

function HorizontalSlider({items, sideArrows, css, theme, severalComponentOnSlide, numberOfComponentOnSlide}, ref) {
    const [viewRef, viewContainerSize] = useElementSize();
    const [elementsContainerRef, elementsContainerSize] = useElementSize([items]);
    const [elementRef, elementSize] = useElementSize();

    const currentPosition = useValue(0);

    useEffect(() => {
        currentPosition.set(0);
    }, [items])

    const canNext = useMemo(() => {
        return viewContainerSize.width < elementsContainerSize.width + currentPosition.value;
    }, [viewContainerSize.width, elementsContainerSize.width, items, currentPosition.value])

    const canPrev = useMemo(() => {
        return currentPosition.value < 0;
    }, [currentPosition.value])

    const slides = useMemo(() => {
        let currentSlideCount = 0;
        const sliders = [];
        let sliderContent = [];
        items.forEach(item => {
            currentSlideCount += 1;
            sliderContent.push(item);
            if (currentSlideCount === numberOfComponentOnSlide) {
                currentSlideCount = 0;
                sliders.push(<SlideContainer items={sliderContent} defaultCountOnSlide={numberOfComponentOnSlide} width={viewContainerSize.width}/>);
                sliderContent = [];
            }
        })
        if (currentSlideCount !== 0) {
            sliders.push(<SlideContainer items={sliderContent} defaultCountOnSlide={numberOfComponentOnSlide} width={viewContainerSize.width}/>);
        }
        return sliders;
    }, [items, viewContainerSize.width, numberOfComponentOnSlide]);

    const onNext = useCallback(() => {
        if (!canNext) {
            return;
        }
        currentPosition.set(currentPosition.value - elementSize.width);
    }, [elementSize.width, currentPosition.value, canNext]);

    const onPrev = useCallback(() => {
        if (!canPrev) {
            return;
        }
        currentPosition.set(currentPosition.value + elementSize.width);
    }, [elementSize.width, currentPosition.value, canPrev]);

    const visibleCss = (isVisible) => {
        if (isVisible) {
            return {
                cursor: "pointer"
            }
        }

        return {
            opacity: 0,
        }
    }

    const startPositionX = useValue(0);
    const animation = useValue(true);

    const onTouchStart = e => {
        // e.stopPropagation();
        animation.set(false);
        startPositionX.set(e.touches[0].clientX);
        e.touches[0].clientY *= 0.2;
    }

    const onTouchEnd = e => {
        // e.stopPropagation();
        animation.set(true);
        startPositionX.set(0);
        if (currentPosition.value > 0) {
            currentPosition.set(0);
        }
        if (currentPosition.value < -(elementsContainerSize.width - elementSize.width)) {
            currentPosition.set(-(elementsContainerSize.width - elementSize.width))
        }
    }

    const onTouchMove = e => {
        const delta = startPositionX.value - e.touches[0].clientX;
        startPositionX.set(e.touches[0].clientX);
        currentPosition.set(currentPosition.value - delta);
    }

    const itemsToRender = useMemo(() => {
        if (severalComponentOnSlide) {
            return slides;
        }
        return items;
    });

    return (
        <div ref={ref} style={{...styles.container,...css}}>
            <div ref={viewRef} style={{position: "relative", overflow: "hidden", height: "max-content", width: "100%"}}
                 onTouchStart={onTouchStart} onTouchMove={onTouchMove} onTouchEnd={onTouchEnd}
            >
                <div
                    ref={elementsContainerRef}
                    style={{
                        // height: "min-content",
                        width: "max-content",
                        display: "flex",
                        flexWrap: "wrap",
                        transition: animation.value ? "300ms" : "",
                        position: "relative",
                        left: `${currentPosition.value}px`
                    }}>
                    {itemsToRender ? itemsToRender.map((item, idx) => (
                            <div ref={idx === 0 ? elementRef : null}
                                 style={{paddingRight: idx === itemsToRender.length - 1 ? "" : "20px"}}>
                                <SliderElement ref={idx === 0 ? elementRef : null}>{item}</SliderElement>
                            </div>
                        ))
                        : <div>Здесь ничего нет</div>}
                </div>
                {!sideArrows && (
                    <Row css={{ width: `${viewContainerSize.width}px`, ...styles.arrows.container}}
                         justify={"space-evenly"}
                         align={"center"}
                    >
                        <div style={{height: "100%"}} onClick={onPrev}>
                            <img src={RightArrow}
                                 style={{
                                     transform: "rotate(180deg)",
                                     height: "15px", ...visibleCss(canPrev), filter: theme === "dark" ? "invert(100%)" : ""
                                 }}
                            />
                        </div>

                        <div style={{height: "100%"}} onClick={onNext}>
                            <img src={RightArrow}
                                 style={{
                                     height: "15px", ...visibleCss(canNext), filter: theme === "dark" ? "invert(100%)" : ""
                                 }}
                            />
                        </div>
                    </Row>
                )}
            </div>
            {!!sideArrows && (
                    <Col css={{...styles.sideArrow, ...styles.sideArrow.rightArrow}} onClick={onNext}>
                        <img src={SmallArrowDown} style={{transform: "rotate(-90deg)", ...visibleCss(canNext), filter: theme === "dark" ? "invert(100%)" : ""}}/>
                    </Col>
                )}
                {!!sideArrows && (
                    <Col css={{...styles.sideArrow, ...styles.sideArrow.leftArrow}} onClick={onPrev}>
                        <img src={SmallArrowDown} style={{transform: "rotate(90deg)", ...visibleCss(canPrev), filter: theme === "dark" ? "invert(100%)" : ""}}/>
                    </Col>
                )}
        </div>

    )
}

export default forwardRef(HorizontalSlider)

const styles = {
    container: {
        position: 'relative',
        width: "100%",
        height: "max-content"
    },
    arrows: {
        container: {
            zIndex: "99",
            paddingTop: "$lg",
            '@sm': {
                paddingTop: "$xl",
            }
        }
    },
    sideArrow: {
        w: 'max-content',
        h: 'max-content',
        position: "absolute",
        top: '50%',
        rightArrow: {
            right: 0,
            transform: 'translate(100%, -50%)',
        },
        leftArrow: {
            left: 0,
            transform: 'translate(-100%, -50%)',
        }
    },
}